
// Шрифты ====================================================================

@font-face {
	font-family: 'ProximaNova';
	src: url('../fonts/ProximaNova-Light.eot');
	src: local('☺'), url('../fonts/ProximaNova-Light.woff') format('woff'), url('../fonts/ProximaNova-Light.ttf') format('truetype'), url('../fonts/ProximaNova-Light.svg') format('svg');
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'ProximaNova';
	src: url('../fonts/ProximaNova-Semibold.eot');
	src: local('☺'), url('../fonts/ProximaNova-Semibold.woff') format('woff'), url('../fonts/ProximaNova-Semibold.ttf') format('truetype'), url('../fonts/ProximaNova-Semibold.svg') format('svg');
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'ProximaNova';
	src: url('../fonts/ProximaNova-Bold.eot');
	src: local('☺'), url('../fonts/ProximaNova-Bold.woff') format('woff'), url('../fonts/ProximaNova-Bold.ttf') format('truetype'), url('../fonts/ProximaNova-Bold.svg') format('svg');
	font-weight: 700;
	font-style: normal;
}



// ===========================================================================


body {
	background-color: #ebebeb;
}

.wrapper {
	overflow: hidden;
	min-height: 100%;
}

.container {
	max-width: 1180px;
	padding: 0;
	margin: 0 auto;

	@media (max-width: 1200px) {
		padding: 0 50px;
	}
	@media (max-width: 590px) {
		padding: 0;
	}
}



// ===========================================================================




/* ==========================================================================
HEADER
========================================================================== */

.header {
	position: absolute;
	width: 100%;
	z-index: 50;
	left: 0;
	top: 0;

	&__row {}

	&__nav {
		height: 42px;
		background-color: #101010;
	}

	&__container {
		@include flexbox();
		max-width: 1180px;
		padding: 0;
		margin: 0 auto;
	}

	&__logo {}

	&__menu {
		height: 70px;
		background-color: #ffffff;
	}

	@media (max-width: 916px) {
		&__logo img {
			width: 0;
			height: 0;
		}
		&__menu {
			height: 0;
		}
	}

	@media (max-width: 350px) {
		&__nav {
			height: 126px;
		}
	}

}

.nav-header {
	@include flexbox();
	justify-content: space-between;
	align-items: center;
	height: 42px;
	width: 100%;

	&__left {
		padding: 0 0 0 31px;
	}

	&__right {
		@include flexbox();

		li {
			display:block;
			height: 100%;

			a {
				display:block;
				line-height: 42px;
				width:100%;
				height:100%;
				padding: 0 12.5px;
			}
		}
	}

	&__link, &__title {
		opacity: 0.8;
		font-size: 10px;
		font-weight: 400;
		text-transform: uppercase;
		color: #999999;
		// padding: 0 0 0 31px;
	}
	&__link:hover {
		color: #fff;
		opacity: 1;
		background-color: #fc7c27;
		transition: 0.5s;
	}

	@media (max-width: 916px) {
		&__right {
			padding: 0 45px 0 0;
		}
	}

	@media (max-width: 750px) {
		&__left {
			position: absolute;
			top:0;
			left: -300px;
			padding: 0;
			width: 0;
		}
		&__right {
			li:first-child a {
				display: none;
			}
		}
		&__title {
			display: none;
		}
	}

	@media (max-width: 350px) {
		height: 126px;
		&__right {
			flex-direction: column;
		}
	}

}

.user {
	@include flexbox();
	flex-direction: row;
	justify-content: center;
	align-items: center;

	&__name {
		font-size: 10px;
		margin: 0 17px 0 0;
	}

	&__icon {
		height: 100%;
	}
}

.menu {
	@include flexbox();
	justify-content: space-between;
	align-items: center;
	height: 70px;
	width: 100%;
	margin: 0 0 0 170px;

	&__left, &__right {
		@include flexbox();
		align-items: center;

		li {
			display:block;
			height: 100%;
			border-right: 1px solid #d5d5d5;

			a {
				display:block;
				line-height: 70px;
				padding: 0 29.6px;
				width:100%;
				height:100%;

				&:hover {
					background-color: #fc7c27;
					transition: 0.5s;
				}
			}
		}

	}

	&__right {
		li {
			&:first-child {
				border-left: 1px solid #d5d5d5;
				a {
					padding: 0 24px;
				}
			}
			a {
				padding: 0 20px;
				img {
					vertical-align: middle;
				}
			}
		}
	}

	&__link {
		font-size: 17px;
		font-weight: 700;
		letter-spacing: -0.025em;
		color: #070707;
	}

	@media (max-width: 916px) {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		overflow: auto;
		background-color: #4d4959;
		padding: 70px 0 20px 0;
		margin: 0;
		flex-direction: column;
		justify-content: flex-start;
		@include tr(0px, -100%, 0px);
		@include anim(0.3s,0s);
		&.active{
			@include tr(0px,0px,0px);
		}

		&__left, &__right {
			flex-direction: column;
			li, li:first-child {
				border: none;
				height: 50px;
				a {
					line-height: 50px;
				}
			}
		}

	}

}




.icon-menu {
	@media (max-width: 916px) {
		display: block;
		position: absolute;
		top: 14px;
		right: 14px;
		width: 30px;
		height: 18px;
		cursor: pointer;
		z-index: 5;
		span {@include anim(0.3s,0s);top: 8px; left: 0; position: absolute; width: 100%; height: 2px; background-color: #fff;
			&:first-child {top: 0;}
			&:last-child {top: auto; bottom: 0;}
		}
		&.active{
			span {@include scale(0);background-color: #fff;
				&:first-child{@include rotate(45deg);top: 8px;}
				&:last-child{@include rotate(-45deg);bottom: 8px;}
			}
		}
	}

}

@media (max-width: 916px) {
	body.lock {
		overflow: hidden;
	}
}




/* ==========================================================================
Slider
========================================================================== */

.slider {
	position: relative;

	&__body {}

	&__item {
		text-align: center;

		&_image img {
			width: 100%;
			height: 100%;
			// max-height: 100vh;
		}
	}

	&__logo {
		left: 50%;
		margin: 0 0 28px 0;
		padding: 0 0 0 10px;
		z-index: 10;
	}
	&__line {
		display: block;
		position: relative;
		content: '';
		width: 70px;
		height: 2px;
		background-color: #ff6600;
		left: 50%;
		z-index: 10;
		margin: 0 0 0 -36px;
	}

	&__title {
		@include fluidFontSize(20px, 80px, 20px, 1600px, 30px);
		position: relative;
		font-size: 80px;
		line-height: 1.2;
		font-weight: 700;
		letter-spacing: -0.05em;
		color: #ffffff;
		z-index: 5;
		margin: 7.2% 0 0.5% 0;

		&:before, &:after {
			content: '';
			position: absolute;
			display: inline-block;
			vertical-align: middle;
			box-sizing: border-box;
			width: 52px;
			height: 1px;
			background-color: #ffffff;
			bottom: 50%;
		}
		&:before {
			left: 100%;
		}
		&:after {
			right: 100%;
		}
	}

	&__text {
		@include fluidFontSize(12px, 20px, 20px, 1600px, 20px);
		text-align: center;
		opacity: 0.67;
		font-size: 20px;
		line-height: 1.2;
		font-weight: 400;
		letter-spacing: -0.05em;
		color: #ffffff;
		z-index: 5;
		margin: 0 0 5.5% 0;
	}

	&__btn {
		@include flexbox();
		justify-content: center;
		align-items: center;
		max-width: 170px;
		height: 50px;
		font-size: 12px;
		line-height: 46px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: -0.025em;
		color: #ffffff;
		padding: 0 20px 0 30px;
		margin: 0 auto;
		border: 2px solid #ffffff;

		img {
			padding: 0 0 0 20px;
		}
		&:hover {
			background: #fc7c27;
			transition: 0.5s;
		}
	}
	
	@media (max-width: 1100px) {
		padding: 0 0 30px 0;
		.slick-dots {
		padding: 30px 0 0 0;
		}
	}

	@media (max-width: 916px) {
		padding: 30px 0;
		&__title {
			margin: 2% 0 0.5% 0;
		}
	}

	@media (max-width: 600px) {
		padding: 30px 0 20px 0;
		.slick-dots {
		padding: 10px 0 0 0;
		}
		&__logo {
			margin: 0 0 14px 0;
		}
		&__title {
			&:before, &:after {
				width: 10px;
			}
		}
		&__text {
			margin: 0 0 3%;
		}
		&__btn {
			height: 40px;
			padding: 0 15px 0 20px;
		}
	}

	@media (max-width: 350px) {
		padding: 124px 0 0 0;
	}
}


.carousel-caption {
	position: absolute;
	top: 24.5%;
	//top: 50.5%;
	left: 0;
	right: 0;
	//transform: translateY(-50%);
	bottom: initial;
	z-index: 5;

	@media (max-width: 916px) {
		top: 10%;
	}
	@media (max-width: 350px) {
		top: 0%;
	}
}

/* Слайдер */
.slick-slider{}
/* Слайдер запущен */
.slick-slider.slick-initialized{}
/* Слайдер с точками */
.slick-slider.slick-dotted{}
/* Ограничивающая оболочка */
.slick-list {
	overflow: hidden;
}
/* Лента слайдов */
.slick-track{
	@include flexbox();
}
/* Слайд */
.slick-slide{}

/* Точки (булиты) */
.slick-dots {
	@include flexbox();
	bottom: 234px;
	align-items: center;
	justify-content: center;

	@media (max-width: 1370px) {
		bottom: 150px;
	}
	@media (max-width: 590px) {
		bottom: 65px;
	}
	@media (max-width: 400px) {
		bottom: 55px;
	}
	@media (max-width: 350px) {
		bottom: 33px;
	}
}
.slider .slick-dots li {
	list-style: none;
}
.slider .slick-dots li {
	width: 4px;
	height: 4px;
	opacity: 0.4;
	background-color: #ffffff;
	border-radius: 50%;
	margin: 0 15px 0 0;

	&:before {
		content: none;
	}
}
/* Активная точка */
.slider li.slick-active {
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background-color: #ffffff;
	opacity: 1;
}
/* Элемент точки */
.slick-dots li button{
	display: none;
}

.slick-slide, .slick-track, .slick-list, .slick-slide-fade {
	position: relative;
}




/* ==========================================================================
Main-screen
========================================================================== */

.page {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	@include flexbox();
	flex-direction: column;
}



