/* ==========================================================================
BLOG
========================================================================== */

.blog {

	&__title {
		padding: 87px 0 0 0;
		margin: 0 0 5px 0;
	}

	&__subtitle {
		margin: 0 0 90px 0;
	}

	&__body {
		@include flexbox();
		flex-direction: column;
	}

	&__row {
		@include flexbox();
	}

	&__column {
		flex: 0 1 50%;
		@include flexbox();
		flex-direction: column;
	}

	&__block {
		@include flexbox();
	}

	&__footer {
		padding: 75px 0 70px 0;
		text-align: center;
	}

	&__btn {
		display: inline-block;
		height: 50px;
		line-height: 46px;
		opacity: 0.78;
		font-size: 12px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: -0.025em;
		color: #000000;
		padding: 0 50px;
		border: 2px solid rgba(84,84,84,0.3);

		&:hover {
			background: #fc7c27;
			transition: 0.5s;
		}
	}

	@media (max-width: 1200px){
		&__footer {
			padding: 50px 0;
		}
		&__btn {
			padding: 0 25px;
		}
	}

	@media (max-width: 700px){

		&__row {
			flex-wrap: wrap;

			&_rev {
				.blog__column {
					@include flexorder(2);
					&_i {
						@include flexorder(1);
					}
				}
			}
		}
		&__column {
		flex: 0 1 100%;
		@include flexbox();
		flex-direction: column;

			&_i {
				flex: 0 1 100%;
				padding: 0 0 100%;
			}
		}

		&__block {
			flex: 0 1 100%;
		}

		&__title {
			padding: 60px 0 0 0;
		}

		&__subtitle {
			margin: 0 0 60px;
		}
	}

}

