}

.button {
	width: 0; 
	height: 0;
	border: 30px solid transparent;
	border-bottom-color: #ff6600;
	position: relative; 
	top: -30px;
	cursor: pointer;
	
	&::after {
		content: '';
		width: 0; 
		height: 0;
		position: absolute;
		top: 30px;
		left: -30px;
		border: 30px solid transparent;
		border-top-color: #ff6600;
	}

	img {
		position: absolute;
		top: 24px;
		left: -8px;
		z-index: 5;
	}
}

.close{
	position: relative;
	width: 13px;
	height: 21px;
	span {
		position: absolute;
		width: 100%;
		height: 1px;
		background-color: #868686;
		&:first-child{@include rotate(45deg);top:10px;}
		&:last-child{@include rotate(-45deg);bottom: 10px;}
	}
}

.nav-footer {
	@include flexbox();
	justify-content: space-between;
	align-items: center;

	&__left {
		@include flexbox();
		li:first-child {
			padding: 0 16px 0 0;
		}
	}

	&__right {
		@include flexbox();
		li:last-child {
			padding: 0 0 0 16px;
		}
	}
	@media (max-width: 1200px){
		&__left {
			li:first-child {padding: 0 14px 0 0;}
		}
		&__right {
			li:last-child {padding: 0 0 0 14px;}
		}
	}

	@media (max-width: 1090px){
		justify-content: space-around;
		&__left, &__right {
			flex-direction: column;
		}
		&__left {
			li:first-child {padding: 0 0 12px 0;}
		}
		&__right {
			li:last-child {padding: 0 0 12px 0;}
		}
	}
}

.social {
	@include flexbox();
	justify-content: center;
	align-items: center;
	text-align: center;

	&__link {
		padding: 0 7.5px;
		&:first-child {padding: 0 7.5px 0 22px;}
		&:last-child {padding: 0 22px 0 7.5px;}

		img {
			border-radius: 50%;

			&:hover {
				background: #fc7c27;
				transition: 0.5s;
			}
		}
	}

	&:before {
		margin: 0 0 0 -50%;
	}
	&:after {
		margin: 0 -50% 0 0;
	}

	&:before, &:after {
		content: '';
		position: relative;
		display: inline-block;
		width: 40%;
		height: 1px;
		vertical-align: middle;
		background: #fff;
		opacity: 0.1;
		//background-clip: content-box;
		//border-bottom: 1px solid rgba(255,255,255,.5);
	}
}

.copy {
	text-align: center;
	font-size: 11px;
	font-weight: 400;
	text-transform: uppercase;
	color: #ffffff;
	opacity: 0.3;
}





// ==========================================================================

.ibg {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	img {display: none;}
}
